#root {
  display: flex;
  flex-direction: column;
  align-items: center;
}
:root {
  --main-font: 'Work Sans', sans-serif;
}
body::-webkit-scrollbar {
  display: none;
}
html, body {
  height: 100%;
  margin: 0;
  color: white;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
.header {
  height: 150px;
  background-color: black;
  display: grid;
  align-items: center;
  grid-template-columns: 4fr 1fr;
  padding: 0 24vw;
  position: sticky;
  top: 0;
}
.title {
  font-size: 2.5vh;
  font-family: var(--main-font);
  letter-spacing: 0.2rem;
}
.navBar {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-self: end;
  gap: 100px;
  font-family: var(--main-font);
  letter-spacing: 0.1rem;
  align-items: center;
  font-size: 2vh;
}
.icon {
  width: 38px;
}
.navItem {
  justify-self: flex-end;
}
.title, .navItem {
  cursor: pointer;
}
.navItem:hover {
  transform: scale(1.15);
}
.carousel {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  margin: 2vh 24vw;
}
.images {
  white-space: nowrap;
  transition: transform 0.4s;
}
.carousel-item {
  display: inline;
  width: 100%;
}
.carousel-img {
  width: 100%;
  border-radius: 20px;
  
}
.carouselButtons {
  position: absolute;
  display: flex;
  gap: 53vw;
}
.carouselPrevious, .carouselNext {
  height: 3vh;
  width: 3vh;
  background-color: rgba(255, 255, 255, 0);
  border: 0;
  cursor: pointer;
}
.carouselPrevious:hover, .carouselNext:hover {
  transform: scale(1.2);
}
a {
  text-decoration: none;
  color: white;
}
#catalog {
  color: black;
  display: grid;
  grid-template-columns: 2fr 5fr;
  min-height: calc(100vh - 150px);
  width: 66vw;
  font-family: var(--main-font);
}
.catalogNav {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;
  box-shadow: 0px 0px 0px 0px black, 1px 0px 5px 0px black;
  background-color: rgb(36, 36, 36);
  color: white;
}
.catalogNavTitle {
  font-size: 2.1rem;
  letter-spacing: 3px;
  text-decoration: underline 1px;
  text-underline-offset: 5px;
  position: fixed;
}
.catalogMain {
  display: grid;
  padding: 20px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  gap: 1px;
}
.product {
  cursor: pointer;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 8px;
  height: 300px;
  border: 2px solid transparent
}
.product:hover {
  border: 2px solid rgb(200, 200, 200);
}
.productImage {
  width: 80%;
  align-self: center;
}
.productPrice {
  font-size: 24px;
}
.productName {
  font-size: 20px;
}
.productPreviousPrice {
  text-decoration: line-through;
}
.catalogNavLink {
  font-size: 1.5rem;
}
.cpu {
  color: black;
  font-size: 22px;
}
.cpuLink {
  position: fixed;
  top: 270px;
}
.gpuLink {
  position: fixed;
  top: 310px;
}
.moboLink {
  position: fixed;
  top: 350px;
}
.ssdLink {
  position: fixed;
  top: 390px;
}